import * as Yup from 'yup';
import { FuncaoFuncionario } from '../../../../models/Pessoas/FuncaoFuncionario';


export const FuncaoFuncionarioSchema = Yup.object().shape<FuncaoFuncionario>({

    nome: Yup.string()
        .nullable()
        .max(50, "Funo do Funcionrio no deve possuir mais de 50 caracteres.")
        .required('Funo do Funcionrio no pode estar vaziu.'),

    cargaHoraria: Yup.number()
        .nullable()
        .min(0.0001, "A Carga Horria no pode estar igual ou inferior a 0.")
        .required('A Carga Horria deve estar informada.'),

    observacao: Yup.string().nullable().max(255, "Observao no pode ter mais de 255 caracteres."),

    descricao: Yup.string().nullable().max(255, "Descrio no pode ter mais de 255 caracteres."),
});
