import * as Yup from 'yup';
import { Estado } from './../../../../models/Cidades/Estado';


export const EstadoSchema = Yup.object().shape<Estado>({
    nome: Yup.string()
        .max(50, "Estado no deve possuir mais de 50 caracteres.")
        .required('Estado no pode estar vaziu.'),

    uf: Yup.string()
        .max(5, "UF no deve possuir mais de 5 caracteres.")
        .required('UF no pode estar vaziu.'),

    paisId: Yup.number()
        .nullable()
        .typeError("Id invlido")
        .required('O campo Pais  obrigatrio'),
});