import * as Yup from 'yup';
import { Cidade } from '../../../../models/Cidades/Cidade';


export const CidadeSchema = Yup.object().shape<Cidade>({
    nome: Yup.string()
        .max(50, "Cidade no deve possuir mais de 50 caracteres.")
        .required('Cidade no pode estar vazia.'),

    ddd: Yup.string()
        .max(5, "DDD no deve possuir mais de 5 caracteres.")
        .required('DDD da Cidade no pode estar vaziu.'),

    estadoId: Yup.number()
        .nullable()
        .typeError("Id invlido")
        .required('O campo Estado  obrigatrio'),
});