import * as Yup from 'yup';
import { Pais } from '../../../../models/Cidades/Pais';


export const PaisSchema = Yup.object().shape<Pais>({
    nome: Yup.string()
        .max(50, "Pas no deve possuir mais de 50 caracteres.")
        .required('Pas no pode estar vaziu.'),

    ddi: Yup.string()
        .max(5, "DDI no deve possuir mais de 5 caracteres.")
        .required('DDI no pode estar vaziu.'),

    sigla: Yup.string()
        .max(5, "Sigla no deve possuir mais de 5 caracteres.")
        .required('Sigla no pode estar vaziu.'),
});