import * as Yup from 'yup';
import { UnidadeMedida } from '../../../../models/Movimentos/UnidadeMedida';


export const UnidadeMedidaSchema = Yup.object().shape<UnidadeMedida>({
    id: Yup.string()
        .max(3, "Cdigo no deve possuir mais de 3 caracteres.")
        .min(1, "Cdigo deve possuir mais de 1 caracter.")
        .required('Cdigo no pode estar vaziu.'),

    nome: Yup.string()
        .max(50, "Unidade Medida no deve possuir mais de 50 caracteres.")
        .required('Unidade Medida no pode estar vaziu.')
});